<template>
  <v-container fluid>
    <v-card class="pa-8">
      <v-row align="center" justify="center">
        <v-col>
          <v-select
            v-model="selectedCategory"
            :items="categories"
            label="Select category"
          ></v-select>
        </v-col>
      </v-row>

      <v-row>
        <v-col>
          <v-select
            v-model="selectedItems"
            multiple
            :items="items"
            label="Select required items"
          ></v-select>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-btn @click.prevent="saveItems">Save</v-btn>
        </v-col>
      </v-row>
    </v-card>
  </v-container>
</template>

<script>
import Axios from "axios";
export default {
  data() {
    return {
      selectedCategory: "",
      selectedItems: [],
      tempObj: {}, //temp obj to create single item objects and add it to postingItems array
      postingItems: [],
      postingObject: {},

      categories: [
        "Surgical",
        "Electronic",
        "Fasion",
        "Cosmetics",
        "Art & Crafts",
        "Automobile",
        "Spices",
        "Food & Drinks",
        "Pharmaceutical",
        "Timber",
        "Households",
        "Sports",
        "Software and Related",
        "Health & Hygiene",
        "Real Estate",
        "Constructions",
      ],
      items: [
        "Masks",
        "Sanitizer",
        "Gloves",
        "Blades",
        "Isoprophyl alcohol",
        "Mobile phones",
        "Kitchenware",
        "Computer & Laptop",
        "Computer Hardware",
        "Outfits",
        "Clothing accessories",
        "Footwear",
        "Creams & Talcs",
        "Cleansing and Scrubs",
        "makeup",
        "Drawings",
        "Handcrafts",
        "Pain Items",
        "Vehicles",
        "Spare parts",
        "Accessories",
        "Spices",
        "Vegetables",
        "Fruits",
        "Snacks",
        "Frizzy",
        "Fresh Juice",
        "Bakery",
        "Other",
        "Tablets",
        "Syrups",
        "Furniture",
        "Crafts",
        "Constructional",
        "Cleaning material",
        "Utility",
        "Clothing",
        "Accessories",
        "Saas",
        "Appications",
        "Administration",
      ],
    };
  },
  methods: {
    saveItems() {
        this.postingObject.name = this.selectedCategory
        for(let i = 0; i < this.selectedItems.length; i++){
            this.tempObj.name = this.selectedItems[i]
            this.postingItems.push(this.tempObj)
            this.tempObj = {}
        }
        this.postingObject.items = this.postingItems
        console.log("posting object")
        console.log(this.postingObject)
      Axios.post("/savecategory", this.postingObject)
        .then((response) => {
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

<style>
</style>


